function debounce(fn, delay){
    let last = {
        time: null,
        timerId:null
    }
    return ()=>{
        let time = Date.now()
        if(last.time && (time - last.time) < delay){
            clearTimeout(last.timerId)
        }
        last = {
            time,
            timerId : setTimeout(fn, delay)
        }
    }
}
/// test

import { test } from 'ava'

test.cb(t=>{
    t.plan(1)
    let counter = 0
    let a = ()=>{
        if(counter===0){
            counter++;
            t.is(1, counter);
            t.end();
        }
    }
    let b = debounce(a, 1000);

    for(let i =0; i < 10; i++){
        b()
    }
})
